const state = {
  setItem:false,
  account:{
    dept:{
      hospitalName:'',
      name:''
    },
    dispName:''
  },
  item:[
    {key:'anPai',name:'',to:'',icon:'book',isDelete:false,color:'#65cbda'},
    {key:'suiFang',name:'',to:'',icon:'people',isDelete:false,color:'#a689b7'},
    {key:'tixing',name:'',to:'',icon:'message',isDelete:false,color:'#f3a9aa'},
    {key:'fenxi',name:'',to:'',icon:'home',isDelete:false,color:'#8ed8e3'},
    
  ],
  dialog:{
    state:false,
    text:'',
    hasCen:false
  },
  patient_state:{
    all_patient:[
      {key:'1',name:'',b:'',age:'11'},
      {key:'2',name:'',b:'',age:'22'},
      {key:'3',name:'',b:'',age:'31'},
      {key:'4',name:'',b:'',age:'11'},
      {key:'5',name:'',b:'',age:'41'},
      {key:'6',name:'',b:'',age:'61'},
      {key:'7',name:'',b:'',age:'21'},
      {key:'8',name:'',b:'',age:'31'},
    ],
    filter_patient:[
      {key:'1',name:'',b:'',age:'11'},
      {key:'2',name:'',b:'',age:'22'},
      {key:'3',name:'',b:'',age:'31'},
      {key:'4',name:'',b:'',age:'11'},
      {key:'5',name:'',b:'',age:'41'},
      {key:'6',name:'',b:'',age:'61'},
      {key:'7',name:'',b:'',age:'21'},
      {key:'8',name:'',b:'',age:'31'},
    ],
    all_filter:{
      key:'fenZu',
      name:'',
      slectText:'',
      icon:'home',
      color:'red',
      list:[
        {key:'huanZheFenZu',type:'filter_text',name:'',icon:'',color:''},
        {key:'feiai1',type:'',name:'',icon:'people',color:'red'},
        {key:'feiai2',type:'',name:'',icon:'people',color:'red'},
        {key:'feiai3',type:'',name:'',icon:'people',color:'red'},
        {key:'feiai4',type:'',name:'',icon:'people',color:'red'},
        {key:'feiai5',type:'',name:'',icon:'people',color:'red'},
        {key:'feiai6',type:'',name:'',icon:'people',color:'red'},
      ]
    },
    all:{
      key:'all',
      name:'',
      slectText:'',
      icon:'home',
      color:'red',
      list:[
        {key:'feiai',name:'',icon:'people',color:'red'},
        {key:'feiai',name:'',icon:'people',color:'red'},
        {key:'feiai',name:'',icon:'people',color:'red'},
      ]
    },
    createTime:{
      key:'createTime',
      name:'',
      slectText:'',
      icon:'home',
      color:'red',
      list:[{key:'feiai',name:'',icon:'people',color:'red'}]
    },
    huanzhe_count:{
      key:'people',
      name:'(340)',
      slectText:'(340)',
      icon:'home',
      color:'red'
      
    }
    
  },
  setting:[
    {key:'duoSheBei',name:'',icons:'home',list:[
      {key:'shaoYiShao',name:'',to:'/'}
    ]},
    {key:'gerenSheZhi',name:'',icons:'people',list:[
      {key:'zhanghao',name:'',to:'/myZhangHao'},
      {key:'xiugaiXinXi',name:'',to:'/xiuGaiXinXi'},
      {key:'xiuGaiMima',name:'',to:'/xiuGaiMiMa'}
    ]},
    {key:'lixianSheZhi',name:'',icons:'settings',list:[
      {key:'lixianShuJu',name:'',to:'/liXianShuJu'},
      {key:'LiXianFAngShi',name:'',to:'/liXianFangShi'}
    ]}
  ]
};
const mutations = {
  add(){
    if(state.setItem) return;
    var g_account = state.account;
    if (g_account.dept && g_account.dept.config && g_account.dept.config.advViewFilter) {
      var filterArry = state.account.dept.config.advViewFilter.map( function (filter, idx) {
          if (!filter.hideInHome)
              return {
                  key: filter.key,
                  name: filter.name,
                  icon: "people",
                  color: '#f3a9aa',
                  to: '/'
              };
      });
      if (g_account.centers && g_account.centers.length > 0) {
        filterArry.push({
            "key": "center",
            "name": "",
            "icon": "people",
            "color": '#f3a9aa',
            "to": '/'
        });
      }
      filterArry.push({key:'add',name:'',to:'',icon:'add',isDelete:false,color:'#d6dee5'})
      state.item = state.item.concat(filterArry);
      state.setItem= true;
    }
  },
  loadDeptStatistics(){

  },
  openDialog(){
    state.dialog.state=true;
  },
  cloneDialog(){
    state.dialog.state=false;
  },
  comPoentsFn(){
    
  },
  succDialog(obj,fn){
    console.log(arguments);
    state.dialog.state=false;
    if(fn){
      return fn();
    }
  },
  setDialogText(obj,text){
    state.dialog.text = text;
  },
  login(obj,account){
    state.account = account?account:{};
  }
};
export default new Vuex.Store({
  state,
  mutations
});



