<template>
  <div class="liXianShuJu_container">
        
        <div class="body">
            <ul>
                <li>
                    <mu-list-item title="" toggleNested :open="toggleNested">
                   
                    <mu-list-item slot="nested" >
                        <p>WIFI</p>
                        <button class="button"></button>
                    </mu-list-item>
                    
                   
                    </mu-list-item>
                    
                </li>
                <li>
                    <mu-list-item title="" toggleNested :open="toggleNested">
                   
                    <mu-list-item slot="nested" >
                        <p></p>
                        <button class="button"></button>
                    </mu-list-item>
                    
                   
                    </mu-list-item>
                    
                </li>
                <li>
                    <mu-list-item title="" toggleNested :open="toggleNested">
                   
                    <mu-list-item slot="nested" >
                        <p></p>
                        <button class="button"></button>
                    </mu-list-item>
                    
                   
                    </mu-list-item>
                    
                </li>
                <li>
                    <mu-list-item title="" toggleNested :open="toggleNested">
                   
                    <mu-list-item slot="nested" >
                        <p></p>
                        <button class="button"></button>
                    </mu-list-item>
                    
                   
                    </mu-list-item>
                    
                </li>
                
            </ul>
        </div>
        <div class="header">
            <div class="header_title"></div>    
            <mu-icon-button icon="android" class="float-button" to="/setup" iconClass="float-button-icon"/> 
            
        </div>
  </div>
    


</template>

<script>
    export default {
        data(){
            return {
                toggleNested:false  
            }
        },
        methods:{
            
        }
    }
</script>


<style  lang="less">
    .liXianShuJu_container{
        position: fixed;
        top:0;
        left:0;
        right:0px;
        bottom:0px;
        background-color:#fff;
        .header{
            position: fixed;
            top:0;
            width:100%;
            height:45px;
            background-color: #285b9a;
            text-align: center;
            .float-button{
                position: absolute;
                top:0;
                left:0;
                float: left;
                .float-button-icon{
                color:#fff;
                }
            }
            .header_title{
                height:45px;
                line-height: 45px;
                color: #fff;
                font-size: 18px;
                text-align: center;
            }
            .baoCun{
                position: absolute;
                right:10px;
                height:45px;
                line-height: 45px;
                color: #fff;
                font-size: 14px;
                margin-right:10px;
            }
        }
        .body{
            margin-top:45px;
            padding:20px;
            ul{
                li{
                    border-bottom:1px solid #ddd;
                    color:#666;
                    p{
                        font-size:12px;
                        margin-bottom:10px;
                    }
                    .button{
                        background-color: #285b9a;
                        border:none;
                        color:#fff;
                        height:30px;
                        border-radius: 4px;
                        width:100%;
                    }
                }
            }
        }
    }
</style>



